<template>
  <div class="about">
    <h1>This is an about page</h1>
    <button @click="toast">toast</button>
    <button @click="imagePick">imagePick</button>
    <button @click="imagePickMulti">imagePickMulti</button>
    <button @click="pressButtonOnOff">pressButtonOnOff</button>
    <button @click="imageCrop">imageCrop</button>
    <button @click="recordingSound">recordingSound</button>
    <button @click="recordingVideo">recordingVideo</button>
    <div v-for="file in files" :key="file.path">
      <img style="width:250px;height:100px" :src="`data:image/png;base64,${file.base64}`"/>
    </div>
  </div>
</template>
<script lang="ts">
import Vue from 'vue'
export default Vue.extend({
  data(){
    return {
        files:[],
        onOff:'on'
    }
  },
  methods:{
    toast(){
      this.$KPG_Toaster('提示框');
    },
    imagePick(){
      this.$KPG_ImagePick('gallery').then(file=>{
        this.files = [file];
      })
    },
    imagePickMulti(){
      this.$KPG_ImagePickMulti(9).then(flies=>{
        this.files = flies;
      })
    },
    pressButtonOnOff(){
      this.onOff = this.onOff=='on'?'off':'on';
      this.$KPG_PressButtonOnOff(this.onOff);
    },
    imageCrop(){
      if(this.files&&this.files.length>0){
        this.$KPG_ImageCrop(this.files[0].path).then(file=>{
            this.files = [file];
        })
      }
    },
    recordingSound(){
      this.$KPG_RecordingSound().then(flies=>{

      })
    },
    recordingVideo(){
      this.$KPG_RecordingVideo().then(flies=>{

      })
    }
  }
})
</script>